  padding-right: 4px; }

/****************************************************************************************/
/* Client navbar */
/****************************************************************************************/
#wrapper-navbar {
  z-index: 1020;
  background-color: #ffffff; }

.navbar {
  padding: 0px;
  background-color: #ffffff; }

.navbar-small {
  -webkit-transition: opacity 0.4s ease-in-out;
  transition: opacity 0.4s ease-in-out; }

.navbar-small .navbar-brand img {
  max-height: 45px;
  width: auto; }

.navbar-nav li a {
  text-transform: uppercase;
  line-height: 1;
  color: rgba(43, 43, 43, 0.77);
  letter-spacing: 0.05em; }

.nav-search,
.nav-toggle {
  display: inline-block;
  line-height: 2; }

.nav-search {
  margin-right: 10px; }

/* drop downs */
.header-navbar .nav .nav-item .nav-link,
.header-navbar .nav .nav-item .dropdown-item {
  padding: 15px; }

.header-navbar .nav .nav-item .dropdown-menu {
  border-top-left-radius: 0px;
  border-top-right-radius: 0px;
  padding-top: 0px;
  padding-bottom: 0px;
  border-radius: 0px;
  margin-top: 32px;
  border: 0px;
  -webkit-animation: fadeInFromNone 0.5s ease-out;
  animation: fadeInFromNone 0.5s ease-out; }

.header-navbar .nav .nav-item .dropdown-menu.show {
  display: block;
  -webkit-animation: fadeInFromNone 0.5s ease-out;
  animation: fadeInFromNone 0.5s ease-out; }

@media only screen and (min-width: 1px) {
  .header-navbar {
    height: 55px; }
  .navbar-collapse {
    background-color: #ffffff; }
  .navbar-brand img {
    width: auto;
    max-height: 45px;
    height: auto; }
  #page .navbar-small-mobile {
    padding-bottom: 0px; }
  .navbar {
    display: block; }
  .navbar .container .navbar-small-buttons {
    -webkit-box-pack: end;
        -ms-flex-pack: end;
            justify-content: flex-end; }
  .btn-nav-search,
  .navbar-toggler {
    margin: 0px;
    padding: 12px; } }

@media only screen and (min-width: 768px) {
  .header-navbar .navbar-brand.c9-custom-logo > .c9-home-logo {
    min-height: 92px; }
  .header-navbar {
    height: 115px; } }

@media only screen and (max-width: 991px) {
  .header-navbar .nav .nav-item .dropdown-menu {
    margin-top: 0px;
    border: 1px solid transparent; } }

@media only screen and (min-width: 992px) {
  .navbar-collapse {
    background-color: transparent; } }

@-webkit-keyframes fadeInFromNone {
  0% {
    display: none;
    opacity: 0; }
  1% {
    display: block;
    opacity: 0; }
  100% {
    display: block;
    opacity: 1; } }

@keyframes fadeInFromNone {
  0% {
    display: none;
    opacity: 0; }
  1% {
    display: block;
    opacity: 0; }
  100% {
    display: block;
    opacity: 1; } }

@-webkit-keyframes slideInUp {
  from {
    -webkit-transform: translate3d(0, 5px, 0);
            transform: translate3d(0, 5px, 0);
    opacity: 0; }
  to {
    -webkit-transform: translate3d(0, 0, 0);
            transform: translate3d(0, 0, 0);
    opacity: 1; } }

@keyframes slideInUp {
  from {
    -webkit-transform: translate3d(0, 5px, 0);
            transform: translate3d(0, 5px, 0);
    opacity: 0; }
  to {
    -webkit-transform: translate3d(0, 0, 0);
            transform: translate3d(0, 0, 0);
    opacity: 1; } }

